import Link from 'components/Link';
import './index.css';

const Footer = () => (
  <footer className="footer">
    <span className="footer__date">
      {`© ${new Date().getFullYear()} Gonzalez Leonel.`}
    </span>
    <Link secondary className="footer__link" href="https://github.com/leogonzalezok" target="_self">
      with love
    </Link>
  </footer>
);

export default Footer;
